import React from 'react';

const requirements = {
	name : {
		min_length:3
	},
	website:{
		min_length:7,
		regex:/http.*/i
	},
	description:{
		min_length:25
	},
	product_name:{
		min_length:4
	},
	product_url:{
		min_length:0,
		regex:/http.*/i
	},
	product_price:{
		min_length:0
	},
	product_description:{
		min_length:25
	},
	product_small_image :{
		min_length:1
	}
}



export function Validation(e, product=false) {
	let name = e.target.name;
	let value = e.target.value;

	if (product) {
		name = 'product_' + name; 
	}

	let reqs = requirements[name];

	//validate length
	if (reqs.min_length !== undefined) {
		if (value.length < reqs.min_length) return "This field must be at least " + reqs.min_length + " characters long.";

		if (reqs.min_length === 0 && value.length === 0) return true;
	}


	//validate regex
	if (reqs.regex !== undefined) {
		if (value.search(reqs.regex) < 0) {
			return "That is not a valid input";
		}
	}


	return true;


}


export function errorMsg(field) {
	if (field === true) return <div className = "success fas fa-check-circle" />;

	return (
		<React.Fragment>
			<div className = "fas fa-exclamation-circle failure" />
			<div className="msg">{field}</div>
		</React.Fragment>
	);
}



